<template>
    <div id="weather">
        <input type="text" class="weather" placeholder="city" v-model="city">
        <button type="submit" @click="search(city)">Submit</button>
        <div class="container">
            <div class="row">
                <div class="col-md-6">
                    <div class="row">
                        <div class="offset-1 col-md-10">
                            <div class="today" style="text-align: center;">
                                <div v-for="(y, index) in listForecast" v-if="index %40===0" :key="y.id">
                                    <div>{{ y.weather[0].icon.slice(0, 2) + ".svg" }}</div>
                                    <div>
                                        <p><span>{{ y.main.temp }}</span></p>
                                        <p><span>{{ y.weather[0].description }}</span></p>
                                        <h1>{{ city }}</h1>
                                        <p><span>{{ todayDate }}</span></p>
                                        <p><span>{{ y.main.pressure }}</span></p>
                                        <p><span>{{ y.main.humidity + '%' }}</span></p>
                                        <p><span>{{ y.wind.speed + 'm/s' }}</span></p>
                                        <p><span>{{ y.main.sea_level }}</span></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-1 col-md-1" v-for="(p, index) in listForecast" v-if="index <8" :key="p.id">
                            <div class="day">
                                <div>{{ p.dt_txt }}</div>
                                <div>{{ p.weather[0].icon.slice(0, 2) + ".svg" }}</div>
                                <div>
                                    <p><span>{{ p.main.temp_max }}</span></p>
                                    <p><span>{{ p.main.temp_min }}</span></p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            
                <div class="col-md-6">
                    <div class="row">
                        <div class="offset-1 col-md-10">
                            <div class="week" style="text-align: center;">
                                <div v-for="(x, index) in listForecast" v-if="index %8===0" :key="x.id">
                                    <div>{{ date }}</div>
                                    <div>{{ x.weather[0].icon.slice(0, 2) + ".svg" }}</div>
                                    <div>
                                        <p><span>{{ x.main.temp_max }}</span></p>
                                        <p><span>{{ x.main.temp_min }}</span></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import moment from 'moment'
import 'moment-timezone'
export default {
    name: 'Weather',
    data () {
        return {
            city: '',
            date: '',
            todayDate: '',
            listForecast: '',
            listForecastOneDay: ''
        }
    },
    methods: {  
        search(city) {
            let url = 'http://api.openweathermap.org/data/2.5/forecast?';
                url += 'APPID=3822751709a7f52738e88ffcd846f0a0';
                url += '&q=' + city; 
                url += '&units=metric';
          this.axios
                .get(url)
                .then(response => {
                    console.log(response);
                    // this.sunrise = new Date(response.data.list[0].sys.sunrise*1000).toLocaleTimeString("en-GB").slice(0,4);
                    // this.sunset = new Date(response.data.list[0].sys.sunset*1000).toLocaleTimeString("en-GB").slice(0,4);
                    this.todayDate = moment().format('llll');
                    for (var i = 0; i < 4; i++) {
                        this.date = moment().add(i, 'days').format('dddd');
                        console.log(this.date);
                    }  
                    this.listForecast = response.data.list;
                })
        },
    }
}
</script>

